import React from 'react';

class Modal extends React.Component {
    constructor(props) {
        super(props);

        this.outerStyle = {
            position: "fixed",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            overflow: "auto",
            zIndex: 1
        };

        // default style
        this.style = {
            modal: {
                position: "relative",
                width: 500,
                // padding: 20,
                boxSizing: "border-box",
                // backgroundColor: "#fff",
                margin: "200px auto",
                borderRadius: 3,
                zIndex: 2,
                textAlign: "left",
                // boxShadow: "0 20px 30px rgba(0, 0, 0, 0.2)",
            },
            overlay: {
                position: "fixed",
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                width: "100%",
                height: "100%",
                backgroundColor: "rgba(0,0,0,0.5)",
            }
        };
    }

    // render modal
    render() {
        return (
            <div
                style={{
                    ...this.outerStyle,
                    display: this.props.isModalOpen ? "block" : "none"
                }}
            >
                <div style={this.style.overlay} onClick={this.props.closeModal} />
                <div style={this.style.modal}>
                    <img
                        width="100%"
                        src={this.props.img}
                        alt="unsplash"
                    /></div>
            </div>
        );
    }
}

export default Modal;